.fade-in-linear-enter-active,
.fade-in-linear-leave-active {
  transition: opacity 200ms linear; }

.fade-in-linear-enter,
.fade-in-linear-leave,
.fade-in-linear-leave-active {
  opacity: 0; }

/** tu-fade-in-linear */
.tu-fade-in-easeout-enter-active,
.tu-fade-in-easeout-leave-active {
  transition: opacity 0.3s ease-out; }

.tu-fade-in-easeout-enter,
.tu-fade-in-easeout-leave,
.tu-fade-in-easeout-leave-active {
  opacity: 0; }

/** tu-fade-in */
.tu-fade-in-enter-active,
.tu-fade-in-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-fade-in-enter,
.tu-fade-in-leave-active {
  opacity: 0; }

/** tu-popper-in-center */
.tu-popper-in-center-enter-active,
.tu-popper-in-center-leave-active {
  transition: all 0.2s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-popper-in-center-enter,
.tu-popper-in-center-leave-active {
  scale: 0;
  transform: scaleX(0); }

/** tu-zoom-in-center */
.tu-zoom-in-center-enter-active,
.tu-zoom-in-center-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-zoom-in-center-enter,
.tu-zoom-in-center-leave-active {
  opacity: 0;
  transform: scaleX(0); }

/** tu-zoom-in-top */
.tu-zoom-in-top-enter-active,
.tu-zoom-in-top-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center top; }

.tu-zoom-in-top-enter,
.tu-zoom-in-top-leave-active {
  opacity: 0;
  transform: scaleY(0); }

.tu-zoom-in-bottom-enter-active,
.tu-zoom-in-bottom-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center bottom; }

.tu-zoom-in-bottom-enter,
.tu-zoom-in-bottom-leave-active {
  opacity: 0;
  transform: scaleY(0); }

/** tu-zoom-in-left */
.tu-zoom-in-left-enter-active,
.tu-zoom-in-left-leave-active {
  opacity: 1;
  transform: scale(1, 1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: top left; }

.tu-zoom-in-left-enter,
.tu-zoom-in-left-leave-active {
  opacity: 0;
  transform: scale(0.45, 0.45); }

.collapse-transition {
  transition: 0.3s height ease-in-out, 0.3s padding-top ease-in-out, 0.3s padding-bottom ease-in-out; }

.horizontal-collapse-transition {
  transition: 0.3s width ease-in-out, 0.3s padding-left ease-in-out, 0.3s padding-right ease-in-out; }

.tu-list-enter-active,
.tu-list-leave-active {
  transition: all 0.48s; }

.tu-list-enter,
.tu-list-leave-active {
  opacity: 0;
  transform: translateX(-40px); }

.tu-cascader-menu-enter-active,
.tu-cascader-menu-leave-active {
  transition: all 0.3s; }

.tu-cascader-menu-enter,
.tu-cascader-menu-leave-active {
  opacity: 0;
  margin-right: -180px; }

.tu-opacity-transition {
  transition: opacity 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

html,
body {
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: #e4ebf5; }

body {
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #8394be;
  -webkit-font-smoothing: antialiased; }

/* BEM [block]__[element]--[modifier]
 -------------------------- */
.tu-alert {
  width: 100%;
  padding: 8px 16px;
  margin: 0;
  box-sizing: border-box;
  border-radius: 4px;
  position: relative;
  overflow: hidden;
  opacity: 1;
  display: flex;
  align-items: center;
  transition: opacity 0.2s;
  color: #8394be; }
  .tu-alert.is-light .tu-alert__closebtn {
    color: #9baacf; }
  .tu-alert.is-dark .tu-alert__closebtn {
    color: #ffffff; }
  .tu-alert.is-dark .tu-alert__description {
    color: #ffffff; }
  .tu-alert.is-center {
    justify-content: center; }
  .tu-alert--default {
    box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff;
    color: #8394be; }
    .tu-alert--default.is-light .tu-alert__description {
      color: #8394be; }
    .tu-alert--default.is-dark {
      color: #9baacf; }
      .tu-alert--default.is-dark .tu-alert__closebtn {
        color: #9baacf; }
  .tu-alert--success.is-light {
    background-color: #f0f9ec;
    color: #6bc43f; }
    .tu-alert--success.is-light .tu-alert__description {
      color: #6bc43f; }
  .tu-alert--success.is-dark {
    background-color: #6bc43f;
    color: #ffffff; }
  .tu-alert--info.is-light {
    background-color: #f4f4f5;
    color: #909399; }
  .tu-alert--info.is-dark {
    background-color: #909399;
    color: #ffffff; }
  .tu-alert--info .tu-alert__description {
    color: #909399; }
  .tu-alert--warning.is-light {
    background-color: #fdf6ec;
    color: #e6a23c; }
    .tu-alert--warning.is-light .tu-alert__description {
      color: #e6a23c; }
  .tu-alert--warning.is-dark {
    background-color: #e6a23c;
    color: #ffffff; }
  .tu-alert--error.is-light {
    background-color: #fef0f0;
    color: #f56c6c; }
    .tu-alert--error.is-light .tu-alert__description {
      color: #f56c6c; }
  .tu-alert--error.is-dark {
    background-color: #f56c6c;
    color: #ffffff; }
  .tu-alert__content {
    display: table-cell;
    padding: 0 8px;
    line-height: 1; }
  .tu-alert__icon {
    font-size: 16px;
    width: 16px; }
    .tu-alert__icon.is-big {
      font-size: 28px;
      width: 28px; }
  .tu-alert__title {
    font-size: 13px;
    line-height: 18px;
    height: 18px; }
    .tu-alert__title.is-bold {
      font-weight: bold; }
  .tu-alert .tu-alert__description {
    font-size: 12px;
    margin: 5px 0 0 0; }
  .tu-alert__closebtn {
    font-size: 12px;
    opacity: 1;
    position: absolute;
    top: 12px;
    right: 15px;
    cursor: pointer;
    transition: opacity 0.3s; }
    .tu-alert__closebtn:hover {
      opacity: 0.8; }
    .tu-alert__closebtn.is-customed {
      font-style: normal;
      font-size: 13px;
      top: 9px; }

.tu-alert-fade-enter,
.tu-alert-fade-leave-active {
  opacity: 0; }
